.app__menuitem {
	width: 100%;
	margin: 1rem 0;
	display: flex;
	flex-direction: column;
}

.app__menuitem-head {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.app__menuitem-sub {
	width: 100%;
	margin-top: 0.2rem;
}

.app__menuitem-name {
	flex: 1;
}

.app__menuitem-dash {
	width: 90px;
	height: 1px;
	flex: none;
	background: var(--color-golden);

	margin: 0 1rem;
}

.app__menuitem-price {
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
}

.app__specialMenu-menu_heading {
	font-family: var(--font-base);
	font-weight: 600;
	font-size: 45px;
	line-height: 58.5px;
	letter-spacing: 0.04em;
	color: var(--color-white);
}
